<template lang="pug">
  div(id="game-root")
    user-panel(
      v-if="!isOnBuildMode"
      :user="user"
      :planet="planet"
      :mode="'Game'"
      @openModal="openModal")
    feature-menu(v-if="!isOnBuildMode && !isSelectBuilding" class="feature-menu" @openModal="openModal" :isModalOpen="!isModalOpen")
    building-info(v-else-if="isSelectBuilding" :building="selectedBuilding" @openModal="openModal" @close="close")
    transition(name="modal")
      modal-card(v-if="isModalOpen" @close="close")
        user-profile(v-if="currentOpen === 'user-profile'" slot="card" :user="user")
        planet-info(v-if="currentOpen === '星球資訊'" slot="card" :user="user" :planet="planet")
        building-upgrade(v-if="currentOpen === '建築升級'" slot="card" :building="selectedBuilding")
        building-feature(v-if="currentOpen === '建築功能'" slot="card" :building="selectedBuilding" :user="user" :planet="planet" @nextScenes="nextScenes")
        building(v-if="currentOpen === '建造頁面'" slot="card" :user="user" :planet="planet" @onBuildMode="onBuildMode")
        warehouse(v-if="currentOpen === '資源頁面'" slot="card" :user="user")
        mission(v-if="currentOpen === '任務頁面'" slot="card" :user="user" :planet='planet')
    build-mode(v-if="isOnBuildMode" @build="close" :building="chooseBuilding")
    Story(
      v-if="actions[currentScenes] === '故事'"
      :currentScenes="currentScenes"
      @nextScenes="nextScenes")
    Dialog(
      v-else
      :planet="planet"
      :currentScenes="currentScenes"
      @nextScenes="nextScenes")
</template>

<script>
import UserPanel from '@/components/user/UserPanel.vue'
import FeatureMenu from '@/components/game/FeatureMenu.vue'
import ModalCard from '@/components/ModalCard.vue'
import UserProfile from '@/components/user/UserProfile.vue'
import Building from '@/components/game/build/Building.vue'
import BuildMode from '@/components/game/build/BuildMode.vue'
import BuildingInfo from '@/components/game/build/BuildingInfo.vue'
import BuildingUpgrade from '@/components/game/build/BuildingUpgrade'
import Warehouse from '@/components/game/warehouse/Warehouse.vue'
import Mission from '@/components/game/mission/Mission.vue'
import BuildingFeature from '@/components/game/build/buildingFeature/BuildingFeature.vue'
import Dialog from '@/components/beginnerGuide/Dialog.vue'
import Story from '@/components/beginnerGuide/Story.vue'
import PlanetInfo from '@/components/game/planetInfo/PlanetInfo.vue'
// import { Obj } from '@/game/scenes/PlayScene.js'
import { mapState, mapGetters, mapMutations, mapActions } from 'vuex'
import beginnerGuideInfo from '@/assets/data/beginner-guide-info.json'

export default {
  name: 'Game',
  components: {
    FeatureMenu,
    UserPanel,
    ModalCard,
    UserProfile,
    Building,
    BuildMode,
    BuildingInfo,
    BuildingUpgrade,
    Warehouse,
    Mission,
    BuildingFeature,
    Dialog,
    Story,
    PlanetInfo
  },
  data () {
    return {
      isModalOpen: false,
      currentOpen: '',
      downloaded: false,
      isOnBuildMode: false,
      module: null,
      module_2: null,
      isSelectBuilding: false,
      selectedBuilding: '',
      chooseBuilding: '',
      actions: beginnerGuideInfo.actions
    }
  },
  mounted () {
    this.$store.watch((state, getters) => {
      return getters
    }, (newValue, oldValue) => {
      this.isSelectBuilding = newValue['planet/getIsSelectBuilding']
      this.selectedBuilding = newValue['planet/getBuildElement']
      if (this.actions[this.currentScenes] === this.selectedBuilding.status) {
        this.nextScenes(this.selectedBuilding.status)
        return
      }
      if (this.actions[this.currentScenes] === '點擊' + this.selectedBuilding.name_zh) {
        this.nextScenes('點擊' + this.selectedBuilding.name_zh)
      }
    }, { deep: true }
    )
  },
  created () {
    import('@/game/scenes/PlayScene').then(async (module) => {
      this.module = module
    })
    import('@/game/scenes/HumanMainScene').then(async (module) => {
      this.module_2 = module
    })
    this.$bus.$on('openModal', this.openModal)
    this.$bus.$on('nextScenes', this.nextScenes)
    this.$bus.$on('close', this.close)
  },
  computed: {
    ...mapState('user', {
      user: state => state
    }),
    ...mapState('planet', {
      planet: state => state.planet
    }),
    ...mapGetters('user', {
      currentScenes: 'getCurrentBeginnerGuideScenes'
    })
  },
  methods: {
    ...mapMutations('user', ['NEXT_SCENES']),
    ...mapActions('user', ['completeBeginner']),
    nextScenes (action) {
      console.log(action, this.actions[this.currentScenes])
      if (this.actions[this.currentScenes] === action) {
        if (this.actions[this.currentScenes + 1] === '儲存') {
          console.log('儲存', this.currentScenes)
          this.NEXT_SCENES()
          this.$store.dispatch('user/nextScenes')
          return true
        }
        this.NEXT_SCENES()
        return true
      }
      if (this.actions[this.currentScenes] === '完成') {
        this.completeBeginner().then(() => {
          this.$router.push('/game')
        })
      }
      return false
    },
    openModal (one) {
      if (this.nextScenes(one)) {
        this.isModalOpen = true
        this.isOnBuildMode = false
        this.currentOpen = one
      }
    },
    close () {
      this.isModalOpen = false
      this.isOnBuildMode = false
      this.module.gameFunction.setIsOnBuildMode(false)
      this.$store.commit('planet/SET_ISSELECTBUILDING', false)
      if (this.nextScenes('離開')) {
      }
    },
    onBuildMode (building) {
      if (this.nextScenes('建造該建築')) {
        this.chooseBuilding = building
        this.isModalOpen = false
        this.isOnBuildMode = true
      }
    }
  },
  watch: {
    isModalOpen: function () {
      this.module.gameFunction.scene.input.manager.enabled = !this.isModalOpen
    }
  },
  beforeDestroy () {
    this.$bus.$off('openModal', this.openModal)
    this.$bus.$off('nextScenes', this.nextScenes)
    this.$bus.$off('close', this.close)
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";

#game-root {
  #game-container {
    height: 100%;
    overflow-y: hidden;
  }
  .feature-menu {
    position: fixed;
    bottom: 20px;
  }
}
.modal-enter-active {
  transition: all 1s 0.5s;
}
.modal-leave-active {
  transition: all 0.5s;
}
.modal-enter,
.modal-leave-to {
  opacity: 0;
}
</style>
